import React, { Component } from 'react';
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import DialogContent from "@material-ui/core/DialogContent";
import Typography from "@material-ui/core/Typography";
import CardMedia from "@material-ui/core/CardMedia";

import PmImage from "../img/PMScreenshot.png"

import FitImageOne from "../img/FitoniaScreenshot1.png"
import FitImageTwo from "../img/FitoniaScreenshot2.png"
import FitImageThree from "../img/FitoniaScreenshot3.png"

import WhiskImage from "../img/MyWhiskApp.png"
import NoImage from "../img/no-image.png"



const projects = [
    {id: 1, title: 'PMPG Simulation tool', content: 'Project sponsored by Humber college. \nThe app is developed on Unity.', icon: "fas fa-tasks", fin: false, img: [PmImage]},
    {id: 2, title: 'Fitonia 2.0', content: 'The newer version of fitness application, with fully operational front-end (React), backend (Node.JS) and database (MongoDB).', icon: "fas fa-circle-notch", fin: false, img: [FitImageOne, FitImageTwo, FitImageThree]},
    {id: 3, title: 'Spatium', content: 'Social network, developed using Vue.JS and Laravel (PHP).', icon: "fas fa-users", fin: false, img: []},
    {id: 4, title: 'MyWhisk', content: 'Android application for getting better pairings for whisky.', icon: "fas fa-glass-whiskey", fin: false, img: [WhiskImage]},
    {id: 5, title: 'Athena', content: 'TensorFlow assistant (in concept stage).', icon: "fas fa-user-graduate", fin: false , img: []},
    {id: 6, title: 'TWL', content: 'Challenge task, made for Shopify Summer internship 2019. Gets .json file from Toronto Waste and filters data to get results.', icon: "fas fa-recycle", fin: true, img: []},
    {id: 7, title: 'Modest', content: 'Landing page with pure HTML and CSS.', icon: "fas fa-pager", fin: true, img: []},
    {id: 8, title: 'Fitonia', content: 'First attempt and developing applications using AngularJS.', icon: "far fa-stop-circle", fin: true, img: []},
    {id: 9, title: 'Stocky', content: 'An application that allows user to follow popular stocks. Developed with React, Django and PostgreSQL.', icon: "far fa-poll", fin: false, img: []},

];

export class WorkInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: true,
            project: {
                title: "test",
                content: "",
                icon: "",
                img: [FitImageOne]
            },
            media: [FitImageOne]
        };
        this.handleClose = this.handleClose.bind(this);
    }

    handleClose(){
        this.props.closeDialog();
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.idOfWork !== this.props.idOfWork){
            let media;
            if(projects[this.props.idOfWork-1].img.length === 0){
                media = [NoImage];
            }else if(projects[this.props.idOfWork-1].img.length > 0){
                media = projects[this.props.idOfWork-1].img;
            }

            this.setState({
                project: projects[this.props.idOfWork-1],
                open: true,
                media: media
            })
        }
    }


    render() {
        return (
            <div>
                <Dialog onClose={this.handleClose} aria-labelledby="simple-dialog-title" open={this.props.dialogOpen} scroll={"body"} fullWidth>
                    <DialogTitle id="simple-dialog-title">{this.state.project.title}</DialogTitle>
                    <CardMedia
                        component="img"
                        style={{minHeight: "400px"}}
                        image={this.state.media[0]}
                        title="Media"
                    />
                    <DialogContent >
                        <Button variant="outlined" color="primary">
                            Github Repo
                        </Button>
                        <Typography gutterBottom>
                            {this.state.project.content}
                        </Typography>

                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} color="primary">
                            Close
                        </Button>

                    </DialogActions>
                </Dialog>
            </div>

        );
    }
}

export default WorkInfo;
